<template>
  <div>
    <h4 class="title">{{ row.title }}</h4>
    <div class="form-item">
      <van-checkbox-group v-model="checkbox" @change="handleChange">
        <van-checkbox :name="item.value" v-for="(item,i) in row.list" v-bind:key="i" shape="square">{{ item.text }}</van-checkbox>
      </van-checkbox-group>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Array,
      default: () => {}
    },
    row: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      checkbox: this.value
    }
  },
  methods: {
    handleChange () {
      this.$emit('input', this.checkbox)
    }
  }
}
</script>

