<template>
  <div>
    <h4 class="title">{{ row.title }}</h4>
    <div class="form-item">
      <van-radio-group v-model="radio" @change="handleChange">
        <van-radio :name="item.value" v-for="(item,i) in row.list" v-bind:key="i">{{ item.text }}</van-radio>
      </van-radio-group>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: String
    },
    row: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      radio: this.value
    }
  },
  methods: {
    handleChange () {
      this.$emit('input', this.radio)
    }
  }
}
</script>

