import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    total: 0,
    listData: []
  },
  getters: {
    getListData: state => state.listData
  },
  mutations: {
    setData: (state, data) => state.listData.push(data)
  },
  actions: {
    save(context, data) {
      context.commit("setData", data);
    }
  },
  modules: {}
});
